import React ,{ Component } from 'react';
import { NavLink } from 'react-router-dom'
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CImg
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
//import { NavLink } from 'react-bootstrap';
import "../../scss/Wallet.scss";
import { FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { faUniversity,faExclamationCircle } from '@fortawesome/free-solid-svg-icons';
import {Base_URL} from "../../CommonRoutes/Api"

class Wallet extends Component {
  constructor(props){
    super(props);
    this.state={
     id:localStorage.getItem("user_id")
      }
    }

    componentDidMount(){
      if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
      {
        this.props.history.push("/");
      }
    }
  render(){
    return (
         <>
        {/* <div className="c-app c-default-layout flex-row align-items-center"> */}
             <CContainer style={{padding:"10px"}}>
        <CRow className="justify-content-center">
            <CCol md="12" lg="12" xl="12" style={{padding:"0px"}}>
              <CCard className="mx-10"  style={{boxShadow: "0px 0px 1px 1px orange",borderRadius:"20px", backgroundColor: '#110730',marginTop:"-1.2rem"}}>
                <CCardBody className="p-10"  >
                  {/* <CForm >  */}
                   <center> <h4 style={{color:"orange"}}>Wallet</h4> </center>
                    {/* <p className="text-muted">SignUp As Customer</p> */}
                    <label style={{color:"white"}}>Total</label>
                        <CInputGroup className="mb-3" >
                          <CInput type="text"
                            placeholder="Rs. 0.00"
                            autoComplete="false" 
                            name="total"
                            //value={this.state.total}
                           // onChange={(event) =>{this.setState({total:event.target.value})}}
                           style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                             />
                           </CInputGroup>
                           <span className="desc_heading" style={{float:"right",marginTop:"-1rem",marginBottom:"0.1rem",color:"red",fontSize: "0.8rem"}}>Available for Withdraw</span>
                           <label style={{color:"white"}}>Amount to withdraw</label> 
                        <CInputGroup className="mb-3" >
                          <CInput type="text"
                             name="amt"
                            placeholder="Enter the amount to withdraw"
                            autoComplete="false"
                           // value={this.state.amt}
                           // onChange={(event) =>{this.setState({amt:event.target.value})}}
                           style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                             />
                           </CInputGroup>
                           <span className="desc_heading" style={{float:"right",marginTop:"-1rem",marginBottom:"0.4rem",color:"red",fontSize: "0.8rem"}}>The minimum withdrawal amount is Rs. 250/-</span>
                           <b> <label style={{color:"white"}}>Bankcard</label> </b>       
                           <CCol col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                                
                                         <FontAwesomeIcon icon={faUniversity} style={{ fontSize: "2rem", marginLeft:"2rem"}} color="white"/>
                                              <NavLink exact to={`/bankcard/${this.state.id}`}><p className="content" style={{backgroundColor:"orange"}}>bank details</p>  </NavLink>
                                     </CCol>
                           
                            {/*   </CForm>  */}
                </CCardBody>
               
              </CCard>
              <CButton 
                            type="submit"
                            value="add"
                            block color="warning"
                            style={{color:"white",marginTop:"-16px"}}
                            fullWidth={true}
                           // onClick={()=>this.submitForm()}
                            >Withdraw Amount</CButton>
                          <p className="tran" style={{textAlign:"center",color:"blueviolet",fontSize:"0.9rem",fontWeight:"bold"}}><u> Transaction</u> </p>
                          <p style={{color:"blueviolet",fontSize:"0.7rem"}}> <FontAwesomeIcon icon={faExclamationCircle} style={{ fontSize: "18px",paddingRight:"4px"}} color="blueviolet"/>
                                                  only single withdraw request accept in month for single bill</p>
            </CCol>
          </CRow>
         </CContainer>
       {/*  </div> */}
       </>
    );
  }
}

export default Wallet;



